          <!-- å§¿åŠ¿è¯†åˆ«ç»“æœ -->
          <div v-if="selectedFunction === 'pose'" class="pose-results">
            <div class="pose-canvas-container">
              <canvas ref="poseCanvas" class="pose-canvas"></canvas>
            </div>
            <div class="pose-info">
              <div>æ£€æµ‹åˆ° {{ recognitionResults.length }} ä¸ªå…³é”®ç‚¹</div>
              <div>ç½®ä¿¡åº¦: {{ averageConfidence.toFixed(1) }}%</div>
            </div>
