              <li>é€‰æ‹©è¦ä½¿ç”¨çš„AIè¯†åˆ«åŠŸèƒ½</li>
              <li>ä¸Šä¼ éœ€è¦è¯†åˆ«çš„å›¾ç‰‡</li>
              <li>ç­‰å¾…æ¨¡å‹åŠ è½½å®Œæˆ</li>
              <li>ç‚¹å‡»"å¼€å§‹è¯†åˆ«"æŸ¥çœ‹ç»“æœ</li>
            </ol>
          </div>
          
          <div class="guide-section">
            <h4>æ³¨æ„äº‹é¡¹</h4>
            <ul>
              <li>é¦–æ¬¡ä½¿ç”¨éœ€è¦ä¸‹è½½AIæ¨¡å‹ï¼Œè¯·è€å¿ƒç­‰å¾…</li>
              <li>å›¾ç‰‡è´¨é‡è¶Šé«˜ï¼Œè¯†åˆ«å‡†ç¡®åº¦è¶Šå¥½</li>
              <li>å§¿åŠ¿è¯†åˆ«éœ€è¦æ¸…æ™°çš„äººä½“å›¾åƒ</li>
              <li>ç‰©ä½“æ£€æµ‹é€‚ç”¨äºåŒ…å«å¤šä¸ªç‰©ä½“çš„åœºæ™¯</li>
            </ul>
          </div>
        </div>
      </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import FileUtil from '@/util/FileUtil'

export default {
  name: 'ImageAIRecognitionView',
  metaInfo: {
    // æ ‡é¢˜ï¼šçªå‡º AI åŠŸèƒ½ã€å·¥å…·ç±»å‹ä»¥åŠä½¿ç”¨çš„æŠ€æœ¯ (ml5.js/æµè§ˆå™¨ç«¯)
    title: 'AI å›¾åƒè¯†åˆ«ä¸åˆ†æå·¥å…· - å®æ—¶åˆ†ç±»ã€å§¿åŠ¿è¯†åˆ« | åŸºäº ml5.js åœ¨çº¿ä½“éªŒ',

    meta: [
      // æè¿°ï¼šå¼ºè°ƒæ ¸å¿ƒåŠŸèƒ½ã€æŠ€æœ¯ä¼˜åŠ¿ï¼ˆæµè§ˆå™¨ç«¯/å…è´¹ï¼‰å’Œåº”ç”¨èŒƒå›´
      {
        name: 'description',
        content: 'å…è´¹åœ¨çº¿ AI å›¾ç‰‡è¯†åˆ«å·¥å…·ã€‚åˆ©ç”¨ ml5.js (TensorFlow.js) åœ¨æµè§ˆå™¨ç«¯è¿›è¡Œå®æ—¶å›¾åƒåˆ†ç±»ã€ç‰©ä½“æ£€æµ‹å’Œäººä½“å§¿åŠ¿è¯†åˆ«ã€‚æ— éœ€ä¸Šä¼ ï¼Œä¿æŠ¤éšç§ï¼Œå³æ—¶åˆ†æå›¾åƒå†…å®¹ã€‚'
      },

      // å…³é”®è¯ï¼šè¦†ç›–æŠ€æœ¯åº“ã€æ ¸å¿ƒåŠŸèƒ½å’Œé€šç”¨æœç´¢è¯
      {
        name: 'keywords',
        content: 'AI å›¾ç‰‡è¯†åˆ«, å›¾åƒåˆ†ç±», å§¿åŠ¿è¯†åˆ«, æœºå™¨å­¦ä¹ åœ¨çº¿, ml5.js, TensorFlow.js, æµè§ˆå™¨ AI åˆ†æ, å…è´¹å›¾ç‰‡è¯†åˆ«å·¥å…·'
      }
    ]
  },
  data() {
    return {
      selectedFunction: 'classification',
      imageFile: null,
      imagePreview: '',
      recognizing: false,
      modelLoading: false,
      modelProgress: 0,
      loadingText: '',
      recognitionResults: [],
      
      // å¯ç”¨åŠŸèƒ½
      functions: [
        {
          id: 'classification',
          name: 'å›¾ç‰‡åˆ†ç±»',
          icon: 'ğŸ·ï¸',
          description: 'è¯†åˆ«å›¾ç‰‡ä¸­çš„ä¸»è¦ç‰©ä½“'
        },
        {
          id: 'pose',
          name: 'å§¿åŠ¿è¯†åˆ«',
          icon: 'ğŸ¤¸',
          description: 'æ£€æµ‹äººä½“å…³é”®ç‚¹'
        },
        {
          id: 'object',
          name: 'ç‰©ä½“æ£€æµ‹',
          icon: 'ğŸ”',
          description: 'è¯†åˆ«å¤šä¸ªç‰©ä½“åŠä½ç½®'
        }
      ],
      
      // ML5æ¨¡å‹
      classifier: null,
      poseNet: null,
      objectDetector: null
    }
  },
  computed: {
    averageConfidence() {
      if (this.recognitionResults.length === 0) return 0
      const sum = this.recognitionResults.reduce((acc, result) => acc + (result.confidence || result.score || 0), 0)
      return (sum / this.recognitionResults.length) * 100
    }
  },
  methods: {
    selectFunction(functionId) {
      this.selectedFunction = functionId
      this.recognitionResults = []
    },

    triggerFileInput() {
      this.$refs.fileInput.click()
    },

    handleFileSelect(event) {
      const file = event.target.files[0]
      if (file) {
        this.loadImage(file)
      }
    },

    handleDrop(event) {
      event.preventDefault()
      const file = event.dataTransfer.files[0]
      if (file && file.type.startsWith('image/')) {
        this.loadImage(file)
      }
    },

    loadImage(file) {
      this.imageFile = file
      const reader = new FileReader()
      reader.onload = (e) => {
        this.imagePreview = e.target.result
      }
      reader.readAsDataURL(file)
      this.recognitionResults = []
    },

    async startRecognition() {
      if (!this.imageFile) return

      this.recognizing = true
      try {
        await this.loadModel()
        await this.performRecognition()
      } catch (error) {
        console.error('è¯†åˆ«å¤±è´¥:', error)
        this.$message.error('è¯†åˆ«å¤±è´¥ï¼Œè¯·é‡è¯•')
      } finally {
        this.recognizing = false
      }
    },

    async loadModel() {
      // æ¨¡æ‹Ÿæ¨¡å‹åŠ è½½è¿‡ç¨‹
      this.modelLoading = true
      this.modelProgress = 0
      
      const modelNames = {
        classification: 'MobileNetå›¾ç‰‡åˆ†ç±»æ¨¡å‹',
        pose: 'PoseNetå§¿åŠ¿è¯†åˆ«æ¨¡å‹',
        object: 'COCO-SSDç‰©ä½“æ£€æµ‹æ¨¡å‹'
      }
      
      this.loadingText = `æ­£åœ¨åŠ è½½${modelNames[this.selectedFunction]}...`
      
      // æ¨¡æ‹ŸåŠ è½½è¿›åº¦
      for (let i = 0; i <= 100; i += 10) {
        this.modelProgress = i
        await new Promise(resolve => setTimeout(resolve, 200))
      }
      
      this.modelLoading = false
      this.$message.success('æ¨¡å‹åŠ è½½å®Œæˆ')
    },

    async performRecognition() {
      const img = new Image()
      img.onload = () => {
        switch (this.selectedFunction) {
          case 'classification':
            this.performClassification(img)
            break
          case 'pose':
            this.performPoseDetection(img)
            break
          case 'object':
            this.performObjectDetection(img)
            break
        }
      }
      img.src = this.imagePreview
    },

    performClassification(img) {
      // æ¨¡æ‹Ÿå›¾ç‰‡åˆ†ç±»ç»“æœ
      console.log('æ­£åœ¨åˆ†æå›¾ç‰‡:', img.src)
      const mockResults = [
        { label: 'çŒ«', confidence: 0.89 },
        { label: 'åŠ¨ç‰©', confidence: 0.76 },
        { label: 'å® ç‰©', confidence: 0.65 },
        { label: 'å“ºä¹³åŠ¨ç‰©', confidence: 0.54 },
        { label: 'å®¶çŒ«', confidence: 0.43 }
      ]
      this.recognitionResults = mockResults
      this.$message.success('å›¾ç‰‡åˆ†ç±»å®Œæˆ')
    },

    performPoseDetection(img) {
      // æ¨¡æ‹Ÿå§¿åŠ¿è¯†åˆ«ç»“æœ
      const mockPoses = [
        { part: 'nose', position: { x: 150, y: 80 }, confidence: 0.95 },
        { part: 'leftEye', position: { x: 140, y: 75 }, confidence: 0.92 },
        { part: 'rightEye', position: { x: 160, y: 75 }, confidence: 0.91 },
        { part: 'leftShoulder', position: { x: 120, y: 140 }, confidence: 0.88 },
        { part: 'rightShoulder', position: { x: 180, y: 140 }, confidence: 0.87 }
      ]
      this.recognitionResults = mockPoses
      this.drawPoseKeypoints(img, mockPoses)
      this.$message.success('å§¿åŠ¿è¯†åˆ«å®Œæˆ')
    },

    performObjectDetection(img) {
      // æ¨¡æ‹Ÿç‰©ä½“æ£€æµ‹ç»“æœ
      const mockObjects = [
        { label: 'äºº', confidence: 0.92, bbox: { x: 50, y: 30, width: 200, height: 300 } },
        { label: 'æ¤…å­', confidence: 0.78, bbox: { x: 20, y: 200, width: 80, height: 120 } },
        { label: 'æ¡Œå­', confidence: 0.65, bbox: { x: 150, y: 250, width: 120, height: 80 } }
      ]
      this.recognitionResults = mockObjects
      this.drawObjectBounds(img, mockObjects)
      this.$message.success('ç‰©ä½“æ£€æµ‹å®Œæˆ')
    },

    drawPoseKeypoints(img, poses) {
      const canvas = this.$refs.poseCanvas
      if (!canvas) return
      
      const ctx = canvas.getContext('2d')
      canvas.width = img.width
      canvas.height = img.height
      
      // ç»˜åˆ¶åŸå›¾
      ctx.drawImage(img, 0, 0)
      
      // ç»˜åˆ¶å…³é”®ç‚¹
      poses.forEach(pose => {
        ctx.fillStyle = '#ff0000'
        ctx.beginPath()
        ctx.arc(pose.position.x, pose.position.y, 5, 0, 2 * Math.PI)
        ctx.fill()
        
        // ç»˜åˆ¶æ ‡ç­¾
        ctx.fillStyle = '#ffffff'
        ctx.font = '12px Arial'
        ctx.fillText(pose.part, pose.position.x + 8, pose.position.y - 8)
      })
    },

    drawObjectBounds(img, objects) {
      const canvas = this.$refs.objectCanvas
      if (!canvas) return
      
      const ctx = canvas.getContext('2d')
      canvas.width = img.width
      canvas.height = img.height
      
      // ç»˜åˆ¶åŸå›¾
      ctx.drawImage(img, 0, 0)
      
      // ç»˜åˆ¶è¾¹ç•Œæ¡†
      objects.forEach((obj, index) => {
        const color = `hsl(${index * 60}, 70%, 50%)`
        ctx.strokeStyle = color
        ctx.lineWidth = 3
        ctx.strokeRect(obj.bbox.x, obj.bbox.y, obj.bbox.width, obj.bbox.height)
        
        // ç»˜åˆ¶æ ‡ç­¾
        ctx.fillStyle = color
        ctx.font = '14px Arial'
        ctx.fillText(
          `${obj.label} (${(obj.confidence * 100).toFixed(1)}%)`,
          obj.bbox.x,
          obj.bbox.y - 5
        )
      })
    },

    formatFileSize(bytes) {
      return FileUtil.formatBytes(bytes)
    }
  }
}
</script>

